module wd{
    export class ImageLoader{
        public static load(url:string, config:AssetConfigData) {
            return wdFrp.fromPromise(new RSVP.Promise((resolve, reject) => {
                var img = null;

                img = new root.Image();

                if(config.isCrossOrigin){
                    img.crossOrigin = "anonymous";
                }

                /*!
                 ieopera

                  firefoxsafari

                 onloadieoperaonload

                 ieoperafirefoxsafarichromeimgsrcurlimg
                 onload

                 imgsrc onloadieoperasrconloadonload
                 onloadsrc
                 */
                img.onload = function () {
                    this.onload = null;     //ie
                    resolve(img);
                };
                img.onerror = function () {
                    reject("error");
                };

                img.src = url;
            }));
        }
    }
}
